import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import Switch from '@material-ui/core/Switch';
import WifiIcon from '@material-ui/icons/Wifi';
import BluetoothIcon from '@material-ui/icons/Bluetooth';

import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/Delete';
import Icon from '@material-ui/core/Icon';

import Avatar from '@material-ui/core/Avatar';

import SubjectIcon from '@material-ui/icons/Subject';

import { 
    Container, 
    Main,
} from '../../styles/Layout';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
  button: {
    margin: theme.spacing(1),
  },
}));

import { useGenericModel } from '../../hooks';

function Solicitacao() {

    const [eventos, setEventos, EventosInicial, addEventos, rmEventos, putEventos, upEventos] = useGenericModel('account_events');
    const [solicitado, setSolicitado ] = React.useState([]);
    const classes = useStyles();

    React.useEffect(() => {
        if(eventos){
            console.log(eventos)
            setSolicitado(eventos.filter( evento =>  evento.status === 0 ))
        }
    },[eventos])


  return (
    <List subheader={<ListSubheader>Settings</ListSubheader>} className={classes.root}>
        {
            solicitado.map( (evento, index) => (
                    <ListItem key={index} >
                        <ListItemIcon>
                            {
                                evento.cliente.cliente?.avatar ?
                                    <Avatar style={{ width: '40px', height: '40px' }}  src={evento.cliente.cliente?.avatar} >
                                        {evento.cliente?.first_name }
                                    </Avatar>
                                :
                                <SubjectIcon />
                            }
                        </ListItemIcon>
                        <ListItemText id={`list-${index}`} secondary={`${evento.servico?.nome} - ${evento.cliente?.first_name }`} />
                        <ListItemSecondaryAction>
                            <Button
                                    variant="contained"
                                    color="primary"
                                    className={classes.button}
                                    startIcon={<DeleteIcon />}
                                >
                                Delete
                            </Button>
                            <Button
                                    variant="contained"
                                    color="default"
                                    className={classes.button}
                                    // endIcon={<Icon>send</Icon>}
                                >
                                Confirmar
                            </Button>
                        </ListItemSecondaryAction>
                    </ListItem>
            ))
        }
    </List>
  );
}

export  { Solicitacao };